    text-shadow: 2px 2px gray;

}
.homeALight, .homePLight, .lanPLight{
    text-shadow: 3px 3px #ff6600;
} */

.homeADark, .homeALight{
    font-size: 60px;
    font-weight: bold;
}
.homePDark, .homePLight{
    font-size: 50px;
}

.lanPDark, .lanPLight{
    font-size: 40px;
    transition: color 0.5s;
}
.lanPDark:hover, .lanPLight:hover{
    color: #517B5B;
    cursor: pointer;
}

.lanDiv{
    display: flex;
    flex-direction: row;
}